.home-main {
    display: flex;
    padding-top: 40px;
    padding-bottom: 70px;
    align-items: center;
    justify-content: center;
    gap: 150px;
  }
  
  .home-image {
    img {
      max-width: 400px;
      width: 100%;
    }
  }
  
  .cursor {
    animation: blink 0.7s infinite;
  }
  
  @keyframes blink {
    0% {
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  
  .home-text {
    h1 {
      font-size: 3rem;
      font-weight: 700;
      color: #000;
      font-family: "Outfit", system-ui;
    }
  
    p {
      font-size: 1.5rem;
      font-weight: 400;
      color: #000;
      font-family: "Moderustic", sans-serif;
    }
  }
  
  .typing-text {
    color: #1E90FF;  // This is dodger blue, a bit lighter
  }
  
  .button-container {
    display: flex;
    gap: 20px;
    margin-top: 20px;
  }
  
  .resume-button,
  .about-button {
    padding: 10px 20px;
    font-size: 1rem;
    font-family: "Moderustic", sans-serif;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .resume-button {
    background-color: #1E90FF;
    color: white;
  
    &:hover {
      background-color: #1873CC;
    }
  }
  
  .about-button {
    background-color: white;
    color: #1E90FF;
    border: 2px solid #1E90FF;
  
    &:hover {
      background-color: #F0F8FF;
    }
  }